import { createRouter, createWebHashHistory } from 'vue-router';
import { componentsList } from './components';
const getRoute = () => {
    const routeListMap = [];
    for (let i = 0; i < componentsList.length; i++) {
        for (let j = 0; j < componentsList[i].items.length; j++) {
            if (componentsList[i].items[j].demo) {
                routeListMap.push({
                    path: `/${componentsList[i].items[j].path}`,
                    name: componentsList[i].items[j].path,
                    meta: {
                        name: componentsList[i].items[j].title
                    },
                    component: () => import('../../../../packages/' + componentsList[i].items[j].path + '/demo/index.vue')
                });
            }
        }
    }
    return routeListMap;
};
const routes = [
    {
        path: '/',
        name: 'Home',
        meta: {
            name: 'Home'
        },
        component: () => import('../components/DemoHome.vue')
    },
    ...getRoute()
];
const router = createRouter({
    history: createWebHashHistory(),
    routes
});
export default router;
